.dialog-overlay {
    animation: fadeIn 0.3s ease;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 50;
  }
  
  .dialog-content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2) !important;
  max-width: 500px;
  width: 90%;
  padding: 24px;
  margin: 8px;
  animation: slideIn 0.3s ease;
  border: none !important;
  max-height: 90vh; /* Set a maximum height */
  overflow-y: auto; /* Enable vertical scrolling */
  }
  
  .dialog-header {
    margin-bottom: 24px;
    text-align: center;
    padding-bottom: 16px;
    border-bottom: 1px solid #e5e7eb;
  }
  
  .dialog-title {
    font-size: 24px;
    font-weight: 600;
    color: #333;
  }
  
  .booking-summary {
    background-color: #f9fafb;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 24px;
    text-align: center;
  }
  
  .booking-summary h2 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 12px;
  }
  
  .booking-details {
    display: flex;
    justify-content: space-between;
    max-width: 300px;
    margin: 0 auto;
  }
  
  .upload-container {
    border: 2px dashed #e2e8f0;
    border-radius: 8px;
    padding: 20px;
    margin-top: 16px;
    transition: all 0.3s ease;
    background-color: #f8fafc;
  }
  
  .upload-container:hover {
    border-color: #4f46e5;
  }
  
  .input-container {
    margin-bottom: 16px;
  }
  
  .input-field {
    width: 100%;
    padding: 8px 12px;
    border: 1px solid #e2e8f0;
    border-radius: 6px;
    margin-top: 4px;
  }
  
  .input-field:focus {
    outline: none;
    border-color: #4f46e5;
    box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.1);
  }
  
  .footer {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    margin-top: 24px;
    padding-top: 16px;
    border-top: 1px solid #e5e7eb;
  }
  
  .button {
    padding: 8px 16px;
    border-radius: 6px;
    font-weight: 500;
    transition: all 0.3s ease;
  }
  
  .button-primary {
    background-color: #4f46e5;
    color: white;
  }
  
  .button-primary:hover {
    background-color: #4338ca;
  }
  
  .button-outline {
    border: 1px solid #e2e8f0;
    color: #64748b;
  }
  
  .button-outline:hover {
    background-color: #f8fafc;
  }
  
  .payment-info {
    background-color: #f9fafb;
    border-radius: 8px;
    padding: 12px;
    margin-top: 16px;
    text-align: center;
  }
  
  .error-alert {
    background-color: #fee2e2;
    border: 1px solid #fecaca;
    color: #dc2626;
    padding: 12px;
    border-radius: 6px;
    margin-top: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes slideIn {
    from {
      transform: translate(-50%, -45%);
      opacity: 0;
    }
    to {
      transform: translate(-50%, -50%);
      opacity: 1;
    }
  }
  
  @media (max-width: 640px) {
    .dialog-content {
      width: 95%;
      padding: 16px;
      margin: 10px;
    }
    
    .dialog-title {
      font-size: 20px;
    }
    
    .footer {
      flex-direction: column;
    }
    
    .button {
      width: 100%;
    }
  }

  .modal-backdrop {
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    inset: 0;
    animation: fadeIn 0.3s ease;
  }
  
  .modal-container {
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 50;
  }

  [data-state="open"][data-backdrop] {
    background-color: rgba(0, 0, 0, 0.5) !important;
    backdrop-filter: blur(1px); /* This adds a slight blur effect to the background */
  }
  
  .dialog-content button[data-dialog-close] {
    display: none;
  }

  button[aria-label="Close"] {
  display: none !important;
  }

  [data-dialog-close],
button[aria-label="Close"] {
  display: none !important;
}

.button-outline {
  display: inline-flex !important;
}

.preview-image {
  max-height: 128px;
  object-fit: contain;
  margin-top: 1rem;
}

.upload-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 1rem;
  border: 2px dashed #ccc;
  border-radius: 0.5rem;
}

.upload-text {
  font-size: 0.875rem;
  color: #666;
}

.upload-hint {
  font-size: 0.75rem;
  color: #888;
  margin-top: 0.5rem;
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.thank-you-container {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@media (max-width: 768px) {
  .seat-selector {
    padding: 10px;
  }

  .seat {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }

  .row-label {
    width: 20px;
    font-size: 14px;
  }

  .seat-row {
    gap: 5px;
  }

  .seat-status {
    flex-wrap: wrap;
    gap: 5px;
  }

  .status {
    padding: 8px 12px;
    font-size: 14px;
  }
}
